<template>
<div id="register">
  <div class="back">
    <van-grid :border="false" :clickable="true">
      <van-grid-item icon="arrow-left" text="返回主页" to="/" @click="$store.commit('setActivePage',0)" />
    </van-grid>
  </div>
  <div class="content">
    <h2 v-if="'register'===Auth">注册</h2>
    <h2 v-if="'register'!==Auth">登录</h2>
    <van-form @submit="onSubmit">
      <van-field
          v-model="username"
          name="username"
          label="用户名"
          placeholder="用户名"
          :rules="[{required: true, message: '用户名未填写'},{ validator:validUser, message: '用户名格式错误'}]"
      />
      <van-field v-if="'register'===Auth"
          v-model="sms"
          center
          clearable
          name="code"
          label="短信验证码"
          placeholder="请输入短信验证码"
          :rules="[{required: true, message: '验证码未填写'}]" :key="'sms'"
      ><!--添加key防止就地复用-->
        <template #button>
          <van-button size="small" type="primary"  native-type="button">发送验证码</van-button>   <!--默认提交改为不提交-->
        </template>
      </van-field>
      <van-field
          v-model="password"
          type="password"
          name="pwd"
          label="密码"
          placeholder="密码"
          :rules="[{ required: true, message: '请填写密码' },{ validator:validUser, message: '密码长度建议4-16'}]"
          autocomplete="off"
          :key="'pwd'"
      />
      <van-field
          v-if="'register'===Auth"
          v-model="confirmPassword"
          type="password"
          name="pwd_"
          label="确认密码"
          placeholder="密码"
          :rules="[{ required: true, message: '请填写密码' },{ validator:validUser, message: '密码长度建议4-16'}]"
          autocomplete="off"

      />
      <p v-if="'register'!==Auth" style="text-align: right;padding: 10px 16px">验证码登录&nbsp&nbsp&nbsp&nbsp></p>
      <div style="margin: 16px;">
        <van-button v-if="'register'===Auth" round block type="info" native-type="submit">注册</van-button>
        <van-button v-if="'register'!==Auth" color="#07bf60" round block type="info" native-type="submit">登录</van-button>
        <van-button class="state" color= "#f98a23" round block type="info" native-type="button" @click="'register'===Auth?Auth='login':Auth='register'">{{ 'register'===Auth?'已注册?去登录':'没账号？去注册' }}</van-button>
      </div>
    </van-form>
  </div>
<!--  <button @click="$router.back()">返回</button>-->
</div>
</template>

<script>

export default {
  name: "register",
  mounted() {

  },
  data() {
    return {

      username: '',
      password: '',
      sms:'',
      confirmPassword:'',   //确认密码
      Auth:'register'    //auth身份验证  //当前为登录or注册状态
    };
  },
  methods: {
      isLoginSuc(){
        if ('登录成功'){
          this.$store.commit('setLoginState',true);
          this.$router.push('/UserInf')
        }
      },
    onSubmit(values) { //表单正确提交的操作
      if ('register'===this.Auth){
        console.log('提交注册的表单',values);
        this.isLoginSuc()
      }else {
        console.log('提交登录的表单',values)
        this.isLoginSuc()
        }
      },
    validUser(value){ //验证的reg
      return this.$regUser(value)
    }
    },



};
</script>

<style scoped>

#register{
  height: 100vh;
}
h2{
  text-align: center;
}
.content{
  width: 90%;
  min-width: 380px;
  margin: 10% auto 0;
}
.van-cell{
  margin-top: 30px;
}
.van-cell__value{
  display: flex;
  flex-direction: row;
}
.state{
  margin-top: 20px;
}
</style>