import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import register from "@/views/register";
import UserInf from "@/views/UserInf";
import Patient from "@/views/Patient";
import addPat from "@/views/addPat";
import addPat_1 from '@/views/addPat_1'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    children:[
      {
        path: '/UserInf',
        name: 'UserInf',
        component: UserInf
      }
    ]
  },
  {
    path: '/register',
    name: 'register',
    component: register
  },
  {
    path: '/Patient',
    name: '/Patient',
    component: Patient
  },
  {
    path: '/addPat',
    name: '/addPat',
    component: addPat
  },
  {
    path: '/addPat_1',
    name: '/addPat_1',
    component: addPat_1
  }
]

const router = new VueRouter({
 //使用历史模式 那么打包为静态时，需改为哈希，否则404需配置ngx服务器
  base: process.env.BASE_URL,
  routes
})

export default router
