import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    loginState:false,  // 当前的登录状态
    activePage:0,     //当前活动页面
    addPatData:null,  //添加就诊人前半部分
    PatData:null,    //完整的就诊人
  },
  mutations: {
    setLoginState(st,val){
      st.loginState = val
    },
    setActivePage(st,val){
      st.activePage = val
    },
    setAddPatData(st,val){
      st.addPatData = val
    }
  },
  actions: {
  },
  modules: {
  }
})
