<template>
  <div class="addPat">
    <div class="back">
      <van-grid :border="false" :clickable="true">
        <van-grid-item icon="arrow-left" text="返回" to="/Patient"/>
      </van-grid>
    </div>

    <div class="container">
      <h1>添加就诊人</h1>
      <p class="title">请确保就诊人信息真实准确，一旦创建成功，不允许修改</p>
      <van-form @submit="onSubmit">
        <template>
          <van-field
              v-model="username"
              name="name"
              label="姓名"
              placeholder="请输入真实姓名"
              error-message-align="right"
              input-align="right"
              :rules="[{ required: true, message: '请填写姓名' }]"
          >
            <template v-slot:label>
              <p style="display: flex;align-items: center"><span
                  style="height: 3px;line-height: 10px;color: red;font-size: 20px">*</span><span style="margin-left: 5px">姓名</span>
              </p>
            </template>
          </van-field>
        </template>  <!--姓名-->
        <template>
          <van-field
              name='sfz'
              v-model="sfz.fieldValue"
              is-link
              readonly
              label="证件类型"
              input-align="right"
              placeholder="居民身份证"
              @click="sfz.show = true"
              error-message-align="right"
              :rules="[{ required: true, message: '请选择身份证' }]"
          >
            <template v-slot:label>
              <p style="display: flex;align-items: center"><span
                  style="height: 3px;line-height: 10px;color: red;font-size: 20px">*</span><span
                  style="margin-left: 5px">证件类型</span></p>
            </template>
          </van-field>
          <van-popup v-model="sfz.show" round position="bottom">
            <van-cascader
                v-model="sfz.cascaderValue"
                title="请选择证件类型"
                :options="sfz.options"
                @close="sfz.show = false"
                @finish="onsfzFinish"
            />
          </van-popup>
        </template>  <!--身份证-->
        <template>
          <van-field
              v-model="sfzID"
              name="sfzID"
              label="证件号"
              placeholder="请输入身份证号"
              error-message-align="right"
              input-align="right"
              :rules="[{ required: true, message: '请填写身份证号' }]"
          >
            <template v-slot:label>
              <p style="display: flex;align-items: center"><span
                  style="height: 3px;line-height: 10px;color: red;font-size: 20px">*</span><span style="margin-left: 5px">证件号</span>
              </p>
            </template>
          </van-field>
        </template>  <!--证件号-->
        <template>
          <van-field
              name='sex'
              v-model="sex.fieldValue"
              is-link
              readonly
              label="性别"
              input-align="right"
              placeholder="选择性别"
              @click="sex.show = true"
              error-message-align="right"
              :rules="[{ required: true, message: '请选择性别' }]"
          >
            <template v-slot:label>
              <p style="display: flex;align-items: center"><span
                  style="height: 3px;line-height: 10px;color: red;font-size: 20px">*</span><span
                  style="margin-left: 5px">选择性别</span></p>
            </template>
          </van-field>
          <van-popup v-model="sex.show" round position="bottom">
            <van-cascader
                v-model="sex.cascaderValue"
                title="请选择您的性别"
                :options="sex.options"
                @close="sex.show = false"
                @finish="onsexFinish"
            />
          </van-popup>
        </template>  <!--性别-->
        <template>
          <van-field
              v-model="date.birthday_"
              name="birthday"
              label="生日"
              placeholder="点击输入生日"
              error-message-align="right"
              input-align="right"
              readonly
              :rules="[{ required: true, message: '请输入生日' }]"
              @click="dateShow=true"
          >
            <template v-slot:label>
              <p style="display: flex;align-items: center"><span
                  style="height: 3px;line-height: 10px;color: red;font-size: 20px">*</span><span style="margin-left: 5px">生日</span>
              </p>
            </template>
          </van-field>
            <van-cell is-link v-show="false">展示弹出层</van-cell>
            <van-popup v-model="dateShow"  position="bottom" :style="{ height: '30%' }">
              <van-datetime-picker
                  v-model="date.birthday"
                  name="birthday"
                  type="date"
                  title="选择年月日"
                  :min-date="date.minDate"
                  :max-date="date.maxDate"
                  @confirm="dateConfirm"
              />
            </van-popup>
        </template>  <!--日期-->
        <template>
          <van-field
              v-model="iphone"
              name="iphone"
              label="手机号"
              placeholder="请输入手机号"
              error-message-align="right"
              input-align="right"
              :rules="[{ required: true, message: '请填写手机号' }]"
          >
            <template v-slot:label>
              <p style="display: flex;align-items: center"><span
                  style="height: 3px;line-height: 10px;color: red;font-size: 20px">*</span><span style="margin-left: 5px">手机号</span>
              </p>
            </template>
          </van-field>
        </template>  <!--手机号-->
        <div style="margin: 16px;">
          <van-button round block type="info" native-type="submit">下一步</van-button>
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "addPat",
  data() {
    return {
      username: '',
      iphone: '',
      sfzID:'',
      sfz:{
        fieldValue: '',
        show: false,
        cascaderValue: '',
        options: [
          {
            text: '身份证',
            value: '1',
          },
          {
            text: '二代身份证',
            value: '2',
          },
        ],
      },
      sex:{
        fieldValue: '',
        show: false,
        cascaderValue: '',
        options: [
          {
            text: '男',
            value: '1',
          },
          {
            text: '女',
            value: '2',
          },{
            text: '其他',
            value: '3',
          },
        ],
      },
      dateShow:false,

      date:{
        minDate: new Date(1997, 0, 1),
        maxDate: new Date(2025, 10, 1),
        birthday: '',
        birthday_:'',
      }

    };
  },
  methods: {
    onSubmit(values) {
      console.log('submit', values);
      this.$store.commit('setAddPatData',values);
      console.log(this.$store.state.addPatData)
      this.$router.push('/addPat_1')

    },
    onsfzFinish({selectedOptions}) {
      this.sfz.show = false;
      this.sfz.fieldValue = selectedOptions.map((option) => option.text).join('/');
    },
    onsexFinish({selectedOptions}) {
      this.sex.show = false;
      this.sex.fieldValue = selectedOptions.map((option) => option.text).join('/');
    },
    dateConfirm(){
      this.dateShow=false;
      let date = this.date.birthday;
      let year = date.getFullYear();
      let month = date.getMonth() + 1; //月份是从0开始的，所以要加1
      let day = date.getDate();
      let formattedDate = year + '年' + month + '月' + day + '日';
      this.date.birthday_ = formattedDate
    }

  },
}
</script>

<style scoped lang="scss">
.container {
  width: 90%;
  margin: 10% auto 0;

  h1 {
    text-align: center;

  }

  .title {
    font-size: 13px;
    text-align: center;
    color: #ef9f60;
    margin: 5% 0;
  }

  ::v-deep .van-form {
    .van-icon {
      margin-top: 5px;
    }

  }
}
.datezj{
  display: none;
}

</style>