<template>
  <div class="addPat_1">
    <div class="back">
      <van-grid :border="false" :clickable="true">
        <van-grid-item icon="arrow-left" text="返回" to="/addPat"/>
      </van-grid>
    </div>
    <div class="container">
      <h1>添加就诊人</h1>
      <p class="title">请确保就诊人信息真实准确，一旦创建成功，不允许修改</p>
      <van-form @submit="onSubmit">
        <template>
          <van-field
              name='nationality'
              v-model="nationality.fieldValue"
              is-link
              readonly
              label="国籍"
              input-align="right"
              placeholder="选择国籍"
              @click="nationality.show = true"
              error-message-align="right"
              :rules="[{ required: true, message: '请选择国籍' }]"
          >
            <template v-slot:label>
              <p style="display: flex;align-items: center"><span
                  style="height: 3px;line-height: 10px;color: red;font-size: 20px">*</span><span
                  style="margin-left: 5px">选择国籍</span></p>
            </template>
          </van-field>
          <van-popup v-model="nationality.show" round position="bottom">
            <van-cascader
                v-model="nationality.cascaderValue"
                title="请选择您的性别"
                :options="nationality.options"
                @close="nationality.show = false"
                @finish="onNationalityFinish"
            />
          </van-popup>
        </template>  <!--国籍-->
        <template>
          <van-field
              name='ethnic'
              v-model="ethnic.fieldValue"
              is-link
              readonly
              label="民族"
              input-align="right"
              placeholder="选择民族"
              @click="ethnic.show = true"
              error-message-align="right"
              :rules="[{ required: true, message: '请选择民族' }]"
          >
            <template v-slot:label>
              <p style="display: flex;align-items: center"><span
                  style="height: 3px;line-height: 10px;color: red;font-size: 20px">*</span><span
                  style="margin-left: 5px">选择民族</span></p>
            </template>
          </van-field>
          <van-popup v-model="ethnic.show" round position="bottom">
            <van-cascader
                v-model="ethnic.cascaderValue"
                title="请选择您的民族"
                :options="ethnic.options"
                @close="ethnic.show = false"
                @finish="onEthnicFinish"
            />
          </van-popup>
        </template>  <!--民族-->
        <template>
          <van-field
              name='marriage'
              v-model="marriage.fieldValue"
              is-link
              readonly
              label="婚姻状况"
              input-align="right"
              placeholder="选择婚姻状况"
              @click="marriage.show = true"
              error-message-align="right"
              :rules="[{ required: true, message: '请选择婚姻状况' }]"
          >
            <template v-slot:label>
              <p style="display: flex;align-items: center"><span
                  style="height: 3px;line-height: 10px;color: red;font-size: 20px">*</span><span
                  style="margin-left: 5px">婚姻状况</span></p>
            </template>
          </van-field>
          <van-popup v-model="marriage.show" round position="bottom">
            <van-cascader
                v-model="marriage.cascaderValue"
                title="请选择您的婚姻状况"
                :options="marriage.options"
                @close="marriage.show = false"
                @finish="onMarriageFinish"
            />
          </van-popup>
        </template>  <!--婚姻-->
        <template>
          <van-field
              name='occupation'
              v-model="occupation.fieldValue"
              is-link
              readonly
              label="职业"
              input-align="right"
              placeholder="选择您的职业"
              @click="occupation.show = true"
              error-message-align="right"
              :rules="[{ required: true, message: '请选择您的职业' }]"
          >
            <template v-slot:label>
              <p style="display: flex;align-items: center"><span
                  style="height: 3px;line-height: 10px;color: red;font-size: 20px">*</span><span
                  style="margin-left: 5px">职业</span></p>
            </template>
          </van-field>
          <van-popup v-model="occupation.show" round position="bottom">
            <van-cascader
                v-model="occupation.cascaderValue"
                title="请选择您的职业"
                :options="occupation.options"
                @close="occupation.show = false"
                @finish="onOccupationFinish"
            />
          </van-popup>
        </template>  <!--职业-->
        <template>
          <van-field
              v-model="username"
              name="unitName"
              label="单位名称"
              placeholder="请输入单位名称"
              error-message-align="right"
              input-align="right"
              :rules="[{ required: true, message: '请填写单位名称' }]"
          >
            <template v-slot:label>
              <p style="display: flex;align-items: center"><span
                  style="height: 3px;line-height: 10px;color: red;font-size: 20px">*</span><span
                  style="margin-left: 5px">单位名称</span>
              </p>
            </template>
          </van-field>
        </template>  <!--单位名称-->
        <template>  <!--住址-->
          <van-field
              v-model="familyArea.area"
              name="familyArea"
              label="家庭地址"
              placeholder="省>市>区"
              error-message-align="right"
              input-align="right"
              readonly
              :rules="[{ required: true, message: '请选择地址' }]"
              @click="familyArea.areaShow=true"
          >
            <template v-slot:label>
              <p style="display: flex;align-items: center"><span
                  style="height: 3px;line-height: 10px;color: red;font-size: 20px">*</span><span
                  style="margin-left: 5px">家庭地址</span>
              </p>
            </template>
          </van-field>
          <van-cell is-link v-show="false">展示弹出层</van-cell>
          <van-popup v-model="familyArea.areaShow" position="bottom" :style="{ height: '30%' }">
            <!--               v-model的值不能给一个四至-->
            <van-area title="地址" :area-list="familyArea.areaList" @confirm="familyAreaConfirm"/>
          </van-popup>
          <template>
            <!--              家庭详细住址-->
            <van-field
                name="familyDetaArea"
                v-model="familyArea.detaArea"
                placeholder="请输入家庭详细地址"

                :rules="[{ required: true, message: '请填写家庭详细地址' }]"
            >
            </van-field>
          </template>
        </template>  <!--家庭地址-->
        <template>
          <van-field
              v-model="currentArea.area"
              name="currentArea"
              label="现居地址"
              placeholder="省>市>区"
              error-message-align="right"
              input-align="right"
              readonly
              :rules="[{ required: true, message: '请选择地址' }]"
              @click="currentArea.areaShow=true"
          >
            <template v-slot:label>
              <p style="display: flex;align-items: center"><span
                  style="height: 3px;line-height: 10px;color: red;font-size: 20px">*</span><span
                  style="margin-left: 5px">现居地址</span>
              </p>
            </template>
          </van-field>
          <van-cell is-link v-show="false">展示弹出层</van-cell>
          <van-popup v-model="currentArea.areaShow" position="bottom" :style="{ height: '30%' }">
            <!--               v-model的值不能给一个四至-->
            <van-area title="地址" :area-list="currentArea.areaList" @confirm="currentAreaConfirm"/>
          </van-popup>
          <template>
            <van-field
                name="currentDetaArea"
                v-model="currentArea.detaArea"
                placeholder="请输入现居详细地址"

                :rules="[{ required: true, message: '请填写现居详细地址' }]"
            >
            </van-field>
          </template>
        </template>  <!--现居地址-->
        <template>
          <van-field
              v-model="Contact.ContactName"
              name="ContactName"
              label="联系人姓名"
              placeholder="请输入联系人姓名"
              error-message-align="right"
              input-align="right"
          >
          </van-field>
        </template>  <!--联系人名称-->
        <template>
          <van-field
              v-model="Contact.Contactsfz"
              name="Contactsfz"
              label="联系人身份证"
              placeholder="请输入联系人身份证"
              error-message-align="right"
              input-align="right"
          >
          </van-field>
        </template>  <!--联系人身份证-->
        <div style="margin: 16px;">
          <van-button round block type="info" native-type="submit">提交</van-button>
        </div>
      </van-form>

    </div>
  </div>
</template>

<script>
import {areaList} from '@vant/area-data';

export default {
  name: "addPat_1",
  created() {
    this.beforeAddPatData = this.$store.state.addPatData;
  },
  data() {
    return {
      beforeAddPatData: null,
      PatData:null,
      username: '',
      Contact: {   //联系人
        ContactName: '',
        Contactsfz: ''
      },
      nationality: {  //国籍选择
        fieldValue: '',
        show: false,
        cascaderValue: '',
        options: [
          {
            text: '中国',
            value: '1',
          },
          {
            text: '老广',
            value: '2',
          }, {
            text: '老美',
            value: '3',
          },
        ],
      },
      ethnic: {  //民族选择
        fieldValue: '',
        show: false,
        cascaderValue: '',
        options: [
          {
            text: '回族',
            value: '1',
          },
          {
            text: '汉族',
            value: '2',
          }, {
            text: '羌族',
            value: '3',
          },
        ],
      },
      marriage: {  //婚姻选择
        fieldValue: '',
        show: false,
        cascaderValue: '',
        options: [
          {
            text: '已婚',
            value: '1',
          },
          {
            text: '未婚',
            value: '2',
          }, {
            text: '丧偶',
            value: '3',
          },
        ],
      },
      occupation: {  //职业选择
        fieldValue: '',
        show: false,
        cascaderValue: '',
        options: [
          {
            text: '学生',
            value: '1',
          },
          {
            text: '工人',
            value: '2',
          }, {
            text: '企业',
            value: '3',
          },
        ],
      },

      familyArea: {
        areaList,
        areaShow: false,
        area: '',
        detaArea: '',
      },
      currentArea: {
        areaList,
        areaShow: false,
        area: '',
        detaArea: '',
      }
    }
  },
  methods: {
    onSubmit(values) {
      console.log('submit', values);
      this.PatData =Object.assign(this.beforeAddPatData,values)
      console.log(this.PatData)
      console.log('结束')
      this.$toast('你好厉害')
    },
    onNationalityFinish({selectedOptions}) {
      this.nationality.show = false;
      this.nationality.fieldValue = selectedOptions.map((option) => option.text).join('/');
    },
    onEthnicFinish({selectedOptions}) {
      this.ethnic.show = false;
      this.ethnic.fieldValue = selectedOptions.map((option) => option.text).join('/');
    },
    onMarriageFinish({selectedOptions}) {
      this.marriage.show = false;
      this.marriage.fieldValue = selectedOptions.map((option) => option.text).join('/');
    },
    onOccupationFinish({selectedOptions}) {
      this.occupation.show = false;
      this.occupation.fieldValue = selectedOptions.map((option) => option.text).join('/');
    },

    familyAreaConfirm(val) {
      let area = '';
      for (let k in val) {
        area += val[k].name + '-'
      }
      this.familyArea.area = area.slice(0, -1)
      this.familyArea.areaShow = false
    },
    currentAreaConfirm(val) {
      let area = '';
      for (let k in val) {
        area += val[k].name + '-'
      }
      this.currentArea.area = area.slice(0, -1)
      this.currentArea.areaShow = false

    }


  }
}
</script>

<style scoped lang="scss">
.container {
  width: 90%;
  margin: 10% auto 0;

  h1 {
    text-align: center;

  }

  .title {
    font-size: 13px;
    text-align: center;
    color: #ef9f60;
    margin: 5% 0;
  }

  ::v-deep .van-form {
    .van-icon {
      margin-top: 5px;
    }

  }

}

</style>